import React, { Component } from 'react'

export class AddItem extends Component {
  render() {
    return (
      <form className={this.props.editMode ? "border p-2 m-2 addForm":"border p-2 m-2 addForm hidden"} id={this.props.subject.replace(/[^A-Z\-0-9]+/ig, "_") +"addForm"}>
        <div className="form-group row">
          <label className="col-sm-2 col-form-label">Assignment</label>
          <div className="col-sm-10">
            <input type="text" onChange={this.props.addAssignmentName.bind(this)} className="form-control" id={this.props.subject.replace(/[^A-Z\-0-9]+/ig, "_") + 'newAssignment'} placeholder="Assignment" />
          </div>
        </div>
        <div className="form-group row">
          <label className="col-sm-2 col-form-label">Description</label>
          <div className="col-sm-10">
            <textarea type="text" onChange={this.props.addAssignmentDescription.bind(this)} className="form-control" id={this.props.subject.replace(/[^A-Z\-0-9]+/ig, "_") + 'newDescription'} placeholder="Description" />
          </div>
        </div>
        
        <div className="form-group row">
          <div className="col-sm-10">
            <button type="button" onClick={this.props.addAssignment.bind(this,this.props.subject)} className="btn btn-primary">Add Assignment</button>
          </div>
        </div>
      </form>
    )
  }
}

export default AddItem
