import React, { Component } from 'react'

export class AssignmentItem extends Component {
  state={
    description:"inactive",
    visible:true,
  }

  showDescription = () => {
    if(this.state.description==='inactive'){
      this.setState({description:'active'})
    }else{
      this.setState({description:'inactive'})
    }
    
  }

  dragStartHandler = () => {
    console.log('picked up')
    this.setState({visible:false})
  }

  dropHandler = () => {
    console.log('set down')
    this.setState({visible:true})
  }

  dragOverHandler = (event) => {
    // console.log(this.props.id)
  }

  render() {

    
    return (
     
    <div className={this.props.hidden || !this.state.visible ? "hidden":''}>
      <div id={this.props.id+'drag'} draggable="true" onDragStart={this.dragStartHandler.bind(this)} onDragEnd={this.dropHandler} onDragOver={this.dragOverHandler.bind(this)} style={{marginBottom:'1rem'}} className={this.props.hidden ? "hidden":'d-flex'}>
          <div className="input-group ">
            <div className="input-group">
              <input type="text" style={{backgroundColor:'white'}} readOnly={this.props.editMode ? false:true} onBlur={this.props.assignmentChange.bind(this,this.props.id,this.props.subject)} onChange={(e)=>this.props.assignmentChangeHandler(e,this.props.id,this.props.subject)} className="form-control assignmentItem"  value={this.props.assignment} />
              <div className="input-group-append text-right">
                <input type="number" className="input-group-text gradeInput text-right " 
                  onBlur={this.props.gradeChange.bind(this,this.props.id,this.props.assignment,this.props.subject,false)} id={this.props.id} 
                  defaultValue={this.props.grade} 
                />              
              </div>
            </div> 
            <textarea className={this.state.description==='inactive' ? 'hidden':''} style={{width:'100%',borderRadius:'0 0 5px 5px'}} readOnly={!this.props.editMode && 'readOnly' } value={this.props.description}> </textarea>
          </div>
          <div>
            <i className="fas fa-info-circle" onClick={this.showDescription.bind(this)}></i>
          </div>
          <div>
            <i className="far fa-eye" id={this.props.id + 'visability'} onClick={this.props.setVisability.bind(this,this.props.id,this.props.subject)}></i>
          </div>
          <div>
            <i id={this.props.id+"trash"} className={this.props.editMode ? "far fa-trash-alt ":'hidden'} onClick={this.props.trashClick.bind(this,this.props.id,this.props.assignment,this.props.subject)}></i>
          </div>
      </div>
      
    </div> 
      
    
    )
  }
}

export default AssignmentItem
