import React, { Component } from "react";
import AddSubject from "./AddSubject";
import PanelItem from "./PanelItem";

export class Main extends Component {
  render() {
    return (
      <div>
        <div className="d-flex">
          <div style={{width:'100px',marginTop:'8px'}} className="form-label">Select Student:</div>
          <select style={{width:'100px'}} className="flex-2 form-control" id="studentSelect" onChange={this.props.studentSelect}>
            
            <option></option>
            {this.props.students.map((student) => <option key={student}>{student}</option>)}
          </select>
        </div>
        <AddSubject editMode={this.props.editMode} addSubject={this.props.addSubject} />
        {this.props.subjects.map(item => {
          if(item.active !== false){
            return (
              <PanelItem
                editMode={this.props.editMode}
                panelMin={this.props.panelMin}
                key={item.subject}
                subject={item.subject}
                trashClick={this.props.trashClick}
                subjectTrashClick={this.props.subjectTrashClick}
                gradeChange={this.props.gradeChange}
                addAssignment={this.props.addAssignment}
                assignmentChange={this.props.assignmentChange}
                fields={item.assignments}
                visible={item.visible}
                addAssignmentName={this.props.addAssignmentName}
                addAssignmentDescription={this.props.addAssignmentDescription}
                setVisability={this.props.setVisability}
                assignmentChangeHandler={this.props.assignmentChangeHandler}
              />
            );
          }else{
            return ''
          }
        })}
      </div>
    );
  }
}

export default Main;
