import React, { Component } from 'react';
import AddItem from './AddItem.js';
import AssignmentItem from './AssignmentItem';

export class PanelItem extends Component {
    render() {
        return (
        <div>
            <div id={this.props.subject.replace(/[^A-Z\-0-9]+/ig, "_")}  key={this.props.subject.replace(/[^A-Z\-0-9]+/ig, "_")} className="card m-1">
                <div className="card-header bg-primary d-flex justify-content-between" id={this.props.subject} onClick={(e)=>this.props.panelMin(e,this.props.subject)}>
                    <h4>{this.props.subject}</h4>
                    <div className="d-flex">
                        <div>
                            <i id={this.props.subject.replace(/[^A-Z\-0-9]+/ig, "_")+"trash"} className={this.props.editMode ? "far fa-trash-alt": "hidden"} onClick={this.props.subjectTrashClick.bind(this,this.props.subject)}></i>
                        </div>                        
                    </div>
                                     
                </div>
                <div className={this.props.visible ? "card-body":"card-body hidden"}>
                    <AddItem editMode={this.props.editMode} addAssignmentName={this.props.addAssignmentName} addAssignmentDescription={this.props.addAssignmentDescription} subject={this.props.subject} addAssignment={this.props.addAssignment} />
                    {
                        this.props.fields.map((item) => {
                            if(item.hasOwnProperty('active') && !item.active){
                                return null
                            }else{
                                return <AssignmentItem  
                                        subject={this.props.subject} 
                                        hidden={item.hidden}
                                        trashClick={this.props.trashClick} 
                                        assignment={item.assignment} 
                                        gradeChange={this.props.gradeChange} 
                                        id={item.id} description={item.description} 
                                        key={item.id} 
                                        grade={item.grade}
                                        editMode={this.props.editMode}
                                        assignmentChange={this.props.assignmentChange} 
                                        setVisability={this.props.setVisability}
                                        assignmentChangeHandler={this.props.assignmentChangeHandler}
                                        />
                            }                            
                        })
                    }
                    <div className="form-group row float-right">
                        <label htmlFor="" className=" col-form-label">Total: </label>
                        <div style={{width:'100px'}} className='ml-1 mr-3'>
                            <div type="text" className="form-control text-right" id={this.props.subject.replace(/[^A-Z\-0-9]+/ig, "_")+'total'}>0</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        );
    }
}

export default PanelItem