import React, { Component } from 'react';
import axios from 'axios';
import './App.css';
import Header from './components/Header'
import SignIn from './components/SignIn'
import Main from './components/Main'

class App extends Component {

  state = {
    authenticated: false,
    teacher: "",
    currentStudent: '',
    availableStudents: '',
    subjects: [],
    editMode: false,
    newAssignment: { name: '', description: '' }
  }

  // host='http://localhost:3001'
  host=''

  componentDidUpdate() {
    if (this.state.editMode) {
      document.querySelectorAll('.hidden').forEach(item => {
        item.classList.remove('hidden')
      })
    }

    if (this.state.authenticated && this.state.currentStudent !== '' && !this.state.dataLoaded) {
      console.log('updated')
      this.state.subjects.forEach(item => {
        if (item.active !== false) {
          item.assignments.forEach(assignment => {
            if (!assignment.grade) { assignment.grade = 0 }
            if (assignment.active || assignment.hasOwnProperty('active') === false) {
              console.log(item.subject.replace(/[^A-Z\-0-9]+/ig, "_"))
              if (!document.querySelector('#' + item.subject.replace(/[^A-Z\-0-9]+/ig, "_") + 'total').innerHTML) {
                document.querySelector('#' + item.subject.replace(/[^A-Z\-0-9]+/ig, "_") + 'total').innerHTML = assignment.grade
              } else {
                document.querySelector('#' + item.subject.replace(/[^A-Z\-0-9]+/ig, "_") + 'total').innerHTML = parseInt(document.querySelector('#' + item.subject.replace(/[^A-Z\-0-9]+/ig, "_") + 'total').innerHTML) + parseInt(assignment.grade)
              }
            }
          })
        }

      })
      this.setState({ dataLoaded: true })
    }
  }

  showSubjectAdd = () => {
    if (this.state.editMode) {
      this.setState({ editMode: false })
    } else {
      this.setState({ editMode: true })
    }
  }

  addSubject = () => {
    let newSubject = document.querySelector('#newSubjectName').value
    if (this.state.subjects.filter((item) => { return item.subject === newSubject }).length === 0) {
      let newState = [...this.state.subjects]
      newState.push({ subject: newSubject, assignments: [] })
      this.setState({ subjects: newState })
      axios.post(this.host+'/newSubject', { teacher: this.state.teacher, student: this.state.currentStudent, subject: newSubject })
        .then((response) => {
          document.querySelector('#newSubjectName').value = ''
        })
    } else {
      alert(newSubject + ' already exists')
    }

  }

  addAssignment = (subject, event) => {
    let newAssignment = {
      active: true,
      id: '',
      assignment: this.state.newAssignment.name,
      description: this.state.newAssignment.description,
      grade: '0'
    }
    let newState = [...this.state.subjects]
    newState.forEach((item) => {
      if (item.subject === subject) {
        newAssignment.id = subject.replace(/[^A-Z\-0-9]+/ig, "_") + (item.assignments.length + 1);
        item.assignments.push(newAssignment)
      }
    })
    this.setState({ subjects: newState })

    newAssignment.teacher = this.state.teacher;
    newAssignment.student = this.state.currentStudent;
    newAssignment.subject = subject
    axios.post(this.host+'/newAssignment', newAssignment)
      .then((response) => {
        document.querySelector('#' + newAssignment.id + 'trash').classList.remove('hidden')
        document.querySelector('#' + subject.replace(/[^A-Z\-0-9]+/ig, "_") + 'newAssignment').value = ''
        document.querySelector('#' + subject.replace(/[^A-Z\-0-9]+/ig, "_") + 'newDescription').value = ''
      })
  }


  subjectTrashClick = (subject) => {
    let newState = [...this.state.subjects]

    newState.filter((item) => { return item.subject === subject })[0].active = false
    this.setState({ newState })
    axios.post(this.host + '/removeSubject', { subject, teacher: this.state.teacher, student: this.state.currentStudent })
      .then(response => {
      })
  }

  trashClick = (id, assignment, subject) => {
    let newState = [...this.state.subjects]
    newState.filter(item => { return item.subject === subject })[0].assignments.filter(item => { return item.id === id })[0].active = false
    this.setState({ newState })
    this.gradeChange(id, assignment, subject, true)
    axios.post(this.host + '/removeAssignment', { id, subject, teacher: this.state.teacher, student: this.state.currentStudent })
      .then(response => {
      })
  }

  signOn = (e) => {
    e.preventDefault()
    axios.post(this.host + '/getdata', { teacher: document.querySelector('#userName').value })
      .then(res => {
        if (res.data !== 'no record') {
          let students = res.data.students.map((student) => student = student.name)
          this.setState({
            teacher: res.data.teacher,
            authenticated: true,
            availableStudents: students
          })
        }
      })
  }

  gradeChange = (id, assignment, subject, test = false) => {
    let newState = [...this.state.subjects]
    let newGrade = document.querySelector('#' + id).value
    newState.filter((item) => { return item.subject === subject })[0].assignments.filter((item) => { return item.id === id })[0].grade = newGrade
    this.setState({ newState })

    document.querySelector('#' + subject.replace(/[^A-Z\-0-9]+/ig, "_") + 'total').innerHTML = 0
    this.state.subjects.filter(item => { return item.subject === subject })[0].assignments.forEach(assignment => {
      if (assignment.hasOwnProperty('active') && !assignment.active) {
        assignment.grade = 0
      } else if (!assignment.grade) {
        assignment.grade = 0
      }

      if (!document.querySelector('#' + subject.replace(/[^A-Z\-0-9]+/ig, "_") + 'total').innerHTML) {
        document.querySelector('#' + subject.replace(/[^A-Z\-0-9]+/ig, "_") + 'total').innerHTML = assignment.grade
      } else {
        document.querySelector('#' + subject.replace(/[^A-Z\-0-9]+/ig, "_") + 'total').innerHTML = parseInt(document.querySelector('#' + subject.replace(/[^A-Z\-0-9]+/ig, "_") + 'total').innerHTML) + parseInt(assignment.grade)
      }
    })
    if (!test) {
      axios.post(this.host + '/gradeUpdate', { id: id, teacher: this.state.teacher, student: this.state.currentStudent, assignment: assignment, subject: subject, grade: newGrade })
        .then((response) => {
        })
    }
  }

  studentSelect = () => {
    let student = document.querySelector('#studentSelect').value
    if (student.toLowerCase() !== this.state.currentStudent.toLowerCase() && student !== '') {
      if (this.state.authenticated) {

        axios.post(this.host + '/studentSelect', { student: student, teacher: this.state.teacher })
          .then((response) => {
            if (response.data !== 'no record') {
              this.setState({ currentStudent: response.data.name, subjects: response.data.subjects, dataLoaded: false })
            } else {
              this.setState({ currentStudent: '', subjects: [], dataLoaded: false })
            }


          })
      }
    }
  }

  assignmentChange = (id, subject,e) => {
    console.log('assignmentchange',e)
    let newAssignmentName = e.target.value
    axios.post(this.host + '/assignmentChange', { newAssignment: newAssignmentName, teacher: this.state.teacher, student: this.state.currentStudent, id: id, subject: subject })
      .then((response => {
        console.log(response)
      }))
  }

  panelMin = (e, subject) => {
    console.log('yep',subject)
    let newState = [...this.state.subjects]
    let newSubject = newState.filter(item => item.subject === subject)[0]
    if (newSubject.visible === true) {
      newSubject.visible = false
    } else {
      newSubject.visible = true
    }

    this.setState({ subjects: newState })
  }

  addAssignmentName = (event) => {
    this.setState({ newAssignment: { name: event.target.value, description: this.state.newAssignment.description } })
  }
  addAssignmentDescription = (event) => {
    this.setState({ newAssignment: { name: this.state.newAssignment.name, description: event.target.value } })
  }

  setVisability = (id, subject, event) => {
    let newState = [...this.state.subjects]
    newState.filter(item => item.subject === subject)[0].assignments.filter(item => item.id === id)[0].hidden = true
    this.setState({ subjects: newState })
  }
  assignmentChangeHandler = (event,id, subject) => {
    console.log(id,subject,event)
    let newState = [...this.state.subjects]
    newState.filter(item => item.subject === subject)[0].assignments.filter(assignment => assignment.id === id)[0].assignment = event.target.value
    this.setState({ sections: newState })
  }


  render() {

    return (
      <div>
        <Header showSubjectAdd={this.showSubjectAdd} />
        <div className="container">

          {(this.state.authenticated === true) ?
            (
              <div>
                <Main
                  editMode={this.state.editMode}
                  panelMin={this.panelMin}
                  subjects={this.state.subjects}
                  students={this.state.availableStudents}
                  addSubject={this.addSubject}
                  addAssignment={this.addAssignment}
                  gradeChange={this.gradeChange}
                  trashClick={this.trashClick}
                  subjectTrashClick={this.subjectTrashClick}
                  studentSelect={this.studentSelect}
                  assignmentChange={this.assignmentChange}
                  addAssignmentName={this.addAssignmentName}
                  addAssignmentDescription={this.addAssignmentDescription}
                  setVisability={this.setVisability}
                  assignmentChangeHandler={this.assignmentChangeHandler}
                />
              </div>
            )
            :
            (<div>
              <SignIn signOn={this.signOn} />
            </div>)}
        </div>
      </div>
    );
  }
}

export default App;
