import React, { Component } from 'react'

export class AddSubject extends Component {
  render() {
    return (
      <div className={this.props.editMode ? "border m-1 addSubject":"border m-1 addSubject hidden"}>  
        <div className="bg-primary top-stripe mb-3"></div> 
        <div className="input-group mb-3">
          <input type="text" className="form-control ml-3" id="newSubjectName" placeholder="New Subject Name" aria-label="Recipient's username" aria-describedby="basic-addon2"/>
          <div className="input-group-append">
            <button className="btn btn-primary mr-3" onClick={this.props.addSubject} type="button">Add Subject</button>
          </div>
        </div>
      </div>
    )
  }
}

export default AddSubject